import Event from 'geval/event';
import clone from '../utils/clone';

class SkinStore {
  constructor(meta, skin) {
    this._onUpdate = this._onUpdate.bind(this);

    this.updates = new Event();

    this.meta = meta;
    this.attributes = {};
    for (const tabName in meta.tabs) {
      const tab = meta.tabs[tabName];
      for (const attribute in tab) {
        this.attributes[attribute] = tab[attribute];
      }
    }

    this.set(skin);
  }

  get() {
    return this.skin;
  }

  set(skin) {
    if (this.unbind) this.unbind();
    this.unbind = () => skin.unbind('update', this._onUpdate);
    skin.bind('update', this._onUpdate);

    this._onUpdate(skin);
  }

  _onUpdate(skin, { attribute, used } = {}) {
    // mark skin switches
    this.switched = !attribute;

    // do nothing if the only thing that changes is the "used" property
    if (used) return;

    // save the modified attribute
    if(attribute) {
      this.lastModifiedAttribute = this.attributes[attribute];
      this.lastModifiedAttribute["name"] = attribute;
    }

    // clone the skin
    this.skin = clone(skin);
    for (const attr in this.attributes) {
      if (this.skin[attr] !== undefined) continue;
      if (this.attributes[attr].options.default == null) continue;
      this.skin[attr] = this.attributes[attr].options.default;
    }

    // broadcas the new skin
    this.updates.broadcast(this.skin);
  }
}

export default SkinStore;
