<script>
import { fade } from "svelte-transitions";
import tinycolor from 'tinycolor2';

export let visible = false;
export let color = '#fff';

$:bc = tinycolor.mostReadable(color, ["#000", "#fff"]).setAlpha(0.5).toRgbString();
</script>

{#if visible}
<div class="loading_icon" transition:fade>
  <style>
    .loading_icon {
      position:absolute;
      width:100%;
      max-width:90px;
      height: 14px;
      top:50%;
      left:50%;
      transform:translate(-50%,-50%);
      text-align:center;
    }

    .loading_icon >i {
      opacity:.5;
      border-radius:50%;
      display:inline-block;
      width:10%;
      padding-top:10%;
      margin:3%;
      transform:scale(.8,.8);
      animation:loading_dots .8s linear infinite;
    }
    .loading_icon >i:nth-child(even){
      animation-delay:.4s
    }
    @keyframes loading_dots {
      0%,100%{opacity:.5;transform:scale(.8,.8)}
      50%{opacity:1;transform:scale(1,1)}
    }
  </style>


  <i style="background-color:{bc};"></i>
  <i style="background-color:{bc};"></i>
  <i style="background-color:{bc};"></i>
</div>
{/if}