/*
  DATA STORE - to save END USERS DATA
*/
// third party libraries
import uxhr from 'uxhr'

// helpers functions
var handle = (resolve, reject) => (res) => {
  // console.log("handle:", res);
  try {
    res = JSON.parse(res)
    // if(res.meta.status === 200) return 
    resolve(res)
    // reject(res)
  } catch (e) {
    reject(e)
  }
}

class DataStore {
  constructor(metadata) {
    // read metadata
    this.metadata = metadata;
    // console.log("DataStore constructor metadata:", metadata);
    
    // read composition id
    this.compositionId = this.metadata.composition.id;
    // console.log("DataStore constructor compositionId:", compositionId);

    // setup api path
    this.apiHost = this.metadata.mode === Widgetic.UI.Composition.EDIT_MODE ? 'http://127.0.0.1:8082' : 'https://widgetic.com';
    this.apiPath = this.apiHost + '/api/v2/compositions/' + this.compositionId + '/data/';
    // console.log("DataStore constructor api path:", this.apiPath);

    // setup headers
    this.headers = {
      'Content-type': 'application/json',
      'X-wdgtc-Version': this.metadata.widget.version,
      'X-wdgtc-Widget':  this.metadata.widget.id,
    }
  }

  // read all end-users data records for current compositionId
  readAllData() {
    // console.log("readAllData apiPath:", this.apiPath)
    return new Promise((resolve, reject) => {
      if(!this.compositionId) reject(new Error('composition id is missing'));

      else uxhr(
        this.apiPath + '?t=' + (new Date()).getTime(),
        null,
        {method: 'GET', headers: this.headers, complete: handle(resolve, reject)}
      )
    })
  }

  // read all data records containing a specific property name and value
  readAllDataWithProperty(dataPropertyName, dataPropertyValue) {
    return new Promise((resolve, reject) => {
      if(!this.compositionId) reject(new Error('CompositionId id is missing'))

      uxhr(
        this.apiPath +
        '?key=' + dataPropertyName +
        '&value=' + dataPropertyValue,
        null,
        {method: 'GET',
        headers: this.headers,
        complete: handle(resolve, reject)}
      )
    })
  }

  // read data record with a specific id
  readDataWithId(dataId) {
    return new Promise((resolve, reject) => {
      if(!dataId) reject(new Error('Data id is missing'))

      uxhr(
        this.apiPath + dataId,
        null,
        {method: 'GET', headers: this.headers, complete: handle(resolve, reject)}
      )
    })
  }  

  // save data record
  saveData(data) {
    return new Promise((resolve, reject) => {
      if(!this.compositionId) reject(new Error('composition id is missing'));

      else uxhr(
        this.apiPath,
        JSON.stringify(data),
        {method: 'POST', headers: this.headers, complete: handle(resolve, reject)}
      )
    })
  }  

  // update data record with id and value
  updateDataWithId(dataId, data) {
    return new Promise((resolve, reject) => {
      if(!dataId) reject(new Error('data id is missing'))
      if(!data) reject(new Error('data is missing'))

      uxhr(
        this.apiPath + dataId,
        JSON.stringify(data),
        {method: 'PUT', headers: this.headers, complete: handle(resolve, reject)}
      )
    })
  }

  // delete data record with a specific id
  deleteDataWithId(dataId) {
    return new Promise((resolve, reject) => {
      if(!dataId) reject(new Error('fact id is missing'))

      uxhr(
        this.apiPath + dataId,
        null,
        {method: 'DELETE', headers: this.headers, complete: handle(resolve, reject)}
      )
    })
  }

  // delete all data records containing a specific property name and value
  deleteDataWithProperty(dataPropertyName, dataPropertyValue) {
    return new Promise((resolve, reject) => {
      if(!this.compositionId) reject(new Error('Composition id is missing'));

      uxhr(
        this.apiPath +
        '?key=' + dataPropertyName +
        '&value=' + dataPropertyValue,
        null,
        {method: 'DELETE',
        headers: this.headers,
        complete: handle(resolve, reject)}
      )
    })
  }
}
export default DataStore;
