/** @module Services/events-store */

import send from '../utils/send';

/**
 * Events service
 *
 * @class EventsStore
 * @example <caption>Adding to the container</caption>
 * import { config, EventsStore } from 'Services';
 * config.service('Events', EventsStore);
 *
 * @example <caption>Dispatch 'startPlaying' event</caption>
 * import { container } from 'Services';
 * container.Events.dispatchPlaying();
 */
class EventsStore {
  constructor() {
  }

  dispatch(eventName, detail) {
    document.dispatchEvent(new CustomEvent(eventName, {detail}));
  }

  /**
   * sends the "startPlaying" message to the parent window's SDK 
   * so it pauses currentPlaying player
   */
  startPlaying() {
    send({ t: 'startPlaying', id: window.name });
  }
}

export default EventsStore;
