/** @module Services/adaptive */
import Event from 'geval/event';
/**
 * Adaptive service
 *
 * @class adaptiveService
 * @example <caption>Adding to the container</caption>
 * import { config, AdaptiveService } from 'Services';
 * config.service('Adaptive', AdaptiveService, 'adaptive');
 *
 * @example <caption>Setting the widgets height</caption>
 * import { container } from 'Services';
 * container.AdaptiveService.setHeight(800)
 */
class AdaptiveService {
  constructor(adaptive) {
    this.onResize = this.onResize.bind(this);
    this.adaptive = adaptive;

    this.updates = new Event();

    window.addEventListener('resize', this.onResize);
    this.onResize();
  }

  onResize() {
    const isOnAdaptive = window.innerWidth <= this.adaptive;

    if(isOnAdaptive !== this.isOnAdaptive) {
      this.updates.broadcast(isOnAdaptive)
    }

    this.isOnAdaptive = isOnAdaptive
  }
}

export default AdaptiveService;
