/** @module Services/autoscale */

import send from '../utils/send';

/**
 * Autoscale service
 *
 * @class AutoscaleService
 * @example <caption>Adding to the container</caption>
 * import { config, AutoscaleService } from 'Services';
 * config.service('Autoscale', AutoscaleService, 'autoscale');
 *
 * @example <caption>Setting the widgets height</caption>
 * import { container } from 'Services';
 * container.Autoscale.setHeight(800)
 */
class AutoscaleService {
  constructor(autoscale) {
    this.onResize = this.onResize.bind(this);
    this.autoscale = autoscale;

    this.width = window.innerWidth;
    this.ratio = null;

    window.addEventListener('resize', this.onResize);
  }

  /**
   * Sets the widget's height using a ratio of it's current width
   * @param {Number} ratio
   */
  setRatio(ratio) {
    this.ratio = ratio;
    this.setHeight(this.width * 1 / ratio);
  }

  /**
   * Sets the widget's height using an absolute value
   * @param {Number} height
   */
  setHeight(height) {

    if (!this.autoscale) return;

    // for autoscale
    send({ t: 'su', id: window.name, d: { height } });

    // for embedly - https://docs.embed.ly/v1.0/docs/provider-height-resizing
    send({
      src: window.location.toString(),
      context: 'iframe.resize',
      height: height // pixels
    });
  }

  onResize(event) {
    this.width = window.innerWidth;
    if (this.ratio) this.setRatio(this.ratio);
  }
}

export default AutoscaleService;
