import WebFont from "webfontloader";
import Event from "geval/event";

const fallbacks = {
  display: 'Copperplate,"Copperplate Gothic Light",fantasy',
  handwriting: '"Brush Script MT",cursive',
  monospace: "Consolas,monaco,monospace",
  "sans-serif": '"Helvetica Neue",Helvetica,Arial,sans-serif',
  serif: "Georgia, Garamond, serif"
};

function parseStyle(style) {
  var isItalic = style.indexOf("italic") >= 0;
  if (isItalic) style = style.replace("italic", "");
  if (style === "regular" || style === "") style = "normal";

  return {
    weight: style,
    style: isItalic ? "italic" : "normal"
  };
}

function fontToCss({ attr, selector, config }) {
  if (!config) return "";
  const { weight, style } = parseStyle(config.style);
  const css = `
  ${selector} {
    font-family: ${config.family},${fallbacks[config.category]};
    font-size: ${config.size}${attr.meta.unit || "px"};
    font-weight: ${weight};
    font-style: ${style};
  }
  `;

  if (config.source === "google") {
    return (
      css +
      `.wf-loading ${selector} { visibility: hidden; }
       .wf-active ${selector}, .wf-inactive ${selector} { visibility: visible; }`
    );
  }

  return css;
}

function loadFont(font, context) {
  // console.log("LOAD FONT FUNCTION FOR FONT:", font);
  const { config } = font;
  if (!config || config.source === "local") return;
  WebFont.load({
    [config.source]: {
      families: [config.family + ":" + config.style]
    },
    timeout: 600,
    context
  });
}

function loadFonts(fonts, context) {
  fonts.forEach(font => loadFont(font, context));
}

export default class FontsService {
  constructor(skinStore, selectorMap = {}) {
    this.skinStore = skinStore;
    this.selectorMap = selectorMap;
    this.updates = new Event();
    this.attributes = [];
    this._parseMeta();
    this.contexts = [window];

    this.skinStore.updates.listen(this._onSkinUpdate.bind(this));
    this._onSkinUpdate(this.skinStore.get());
  }

  addContext(context) {
    this.contexts.push(context);
    loadFonts(this._getFonts(), context);
  }

  removeContext(context) {
    const index = this.contexts.indexOf(context);
    if (index > -1) this.contexts.splice(index, 1);
  }

  _parseMeta() {
    const meta = Object.values(this.skinStore.meta.tabs).reduce(
      (acc, obj) => Object.assign(acc, obj),
      {}
    );

    for (let attr in meta) {
      if (meta[attr].control !== "core/controls/font") continue;
      this.attributes.push({ name: attr, meta: meta[attr].options });
    }
  }

  _getFonts() {
    return this.attributes.map(attr => ({
      attr,
      selector: this.selectorMap[attr.name] || "." + attr.name,
      config: this.skin[attr.name]
    }));
  }

  _onSkinUpdate(skin) {

    if(!this.skinStore.lastModifiedAttribute ||
      (this.skinStore.lastModifiedAttribute && this.skinStore.lastModifiedAttribute.control == "core/controls/font"))
    {
      // update the skin
      this.skin = skin;

      // load the new fonts in all contexts
      const fonts = this._getFonts();
      this.contexts.forEach(context => {
        // load only the font that was modified
        if(this.skinStore.lastModifiedAttribute) {
          fonts.forEach(font => {
              if(font.attr.name == this.skinStore.lastModifiedAttribute.name)
              {
                // console.log("LOAD FONT:", font.attr.name, this.skinStore.lastModifiedAttribute.name);
                loadFont(font, context);

                // update the style for the modified font only
                // let singleFontArray = [font];
                // this.css = singleFontArray.reduce((css, font) => css + fontToCss(font), "");
              }
            });
        }
        // load all fonts
        else {
          // console.log("LOAD ALL FONTS!");
          loadFonts(fonts, context);

          // update the style for all fonts
          // this.css = fonts.reduce((css, font) => css + fontToCss(font), "");
        }
      });

      // update the style for all fonts
      this.css = fonts.reduce((css, font) => css + fontToCss(font), "");

      // update the css property
      this.updates.broadcast(this.css);
    }
  }
}
