import Event from 'geval/event';
import clone from '../utils/clone';
import debounce from 'debounce';

// static instagram and social instagam sources were erroneously configured in content.json using the same name
// TODO migrate or fix the saved compositions to use distinct service names
const isStaticInstagram = item =>
  item.metadata.service === 'instagramapi' && item.metadata.type === 'image';
const dynamicSources = ['instagramapi'];

const isDynamicSource = item =>
  item.metadata &&
  dynamicSources.indexOf(item.metadata.service) >= 0 &&
  !isStaticInstagram(item);

/**
 * # Design
 *
 * Un widget poate avea asociate, pe langa continut adaugat manual (folosind
 * editorul, declarative api sau dynamic api), unul sau mai multe surse de
 * continut dinamic (retele sociale, google spreadsheets, custom endpoints,
 * etc). Serviciul client-side ContentStore interogheaza serverul conform
 * configuratiei widgetului si expune elementele media ca un stream unitar
 * sortat si uniform.
 *
 * Sursele de continut social permit interogarea diverselor servicii sociale
 * pentru itemi media conform unor criterii definite de user (postate de un
 * anumit user; cu un anumit hashtag; etc).
 *
 * API ContentStore:
 *
 * * ContentStore.get() - Reda elementele media deja incarcate.
 * Datele sunt populate de o incarcare initiala cand se initializeaza widgetul
 * (in embed) sau cand se modifica configuratia content (in editor).
 * Incarcarea initiala contine: toate elementele adaugate manual;
 * toate elementele adaugate prin declarative api; primii N itemi din fiecare
 * sursa externa.
 *
 * * ContentStore.load(N) - Aduce fix N media items din toate sursele, inlocuind
 *  ceea ce este deja
 *
 * * ContentStore.hasMore() - Mai sunt fisiere media de adus?
 *
 * * ContentStore.fetch(N) - Aduce inca N itemi din toate sursele. Sursele sunt
 * interogate conform algoritmului de mai jos. Datele redate vor fi tot timpul ordonate (coronologic|popularitate?).
 *
 * Fiecare sursa este reprezentata de o clasa gen InstagramSource, cu acelasi
 * API ca ContentStore
 *
 * ## Algoritm mixare date din mai multe surse:
 *
 * * fetch(10) -> se cer 10 de poze din fiecare sursa, se stocheaza in mem si
 * se sorteaza -> desi ai 20 de itemi, returnezi doar 10; itemii afisati sunt
 * sortati corect.
 * * loadMore(10) -> se cer de la fiecare sursa ~~inca 10 de poze~~ cate poze
 * neafisate am deja in memorie - cate poze sunt cerute, se adauga la cele
 * stocate deja in mem, se sorteaza tot calupul -> primele 10 vor coincide cu
 * cele deja returnate; am in mem 30 de itemi, dar sunt afisati doar 20
 *
 * fetch(10)
 * ?q=A&num=10
 * ?q=B&num=10
 * [ABAABAABAA]AABBABBBBB
 *
 * loadMore(10)
 * ?q=A&num=7
 * ?q=B&num=3
 * [ABAABAABAAAABBA]BAAABAAAAB
 */
class ContentStore {
  constructor(meta, sources, declarativeMap, ContentModel) {
    this.refresh = this.refresh.bind(this);
    this._mapDeclarative = this._mapDeclarative.bind(this);

    this.updates = new Event();
    this.loading = new Event();
    this.loaded = new Event();
    this.error = new Event();

    this.meta = meta;
    this.mainAttribute = this.meta.options.mainAttribute;
    this.sources = sources;
    this.declarativeMap = declarativeMap;
    this.mappedAttributes = [];
    for (let attr in this.declarativeMap) {
      this.mappedAttributes.push(this.declarativeMap[attr]);
    }

    this.content = [];
    this.dynamicContent = [];

    this.ContentModel = ContentModel;
    this.ContentModel.bind('change', debounce(this.refresh, 0));
    this.refresh();
  }

  get() {
    return this.content;
  }

  refresh() {
    this.staticContent = this.ContentModel.all()
      .filter(item => !isDynamicSource(item))
      .map(this._mapDeclarative)
      // ensure that the widget only sees items with the mainAttribute filled in
      .filter(item => !!item[this.mainAttribute])
      // TODO .map(this._applyDefaults)
      .map(item => clone(item));
    this._refresh();

    this.dynamicSources = this.ContentModel.all()
      .filter(item => isDynamicSource(item))
      .filter(config => {
        const service = config.metadata.service;
        const hasSource = !!this.sources[service];
        if (!hasSource) {
          console.error(new Error('No source configured for ' + service));
        }
        return hasSource;
      })
      .map(config => new this.sources[config.metadata.service].class(config));

    this.load(this._lastLoad);
  }

  _refresh() {
    this.content = this.staticContent.concat(this.dynamicContent);
    this.updates.broadcast(this.content);
  }

  load(num) {
    if (!num) return Promise.resolve();
    this.dynamicContent = [];
    this._lastLoad = num;

    this.loading.broadcast();
    return new Promise((resolve, reject) => {
      this._cancel && this._cancel();
      this._cancel = () => reject(new Error('Canceled'));

      // load from all sources; catch errors to be sent off-band on the error event channel
      Promise.all(
        this.dynamicSources.map(source => source.load(num).catch(err => err))
      ).then(resolve, reject);
    })
      .then(results => {
        this.loaded.broadcast();

        const media = results.reduce((acc, result) => {
          if (result instanceof Error) {
            // ignore canceled loads (calling load multiple times cancels the last call)
            // broadcast the error off-band
            if (result.message !== 'Canceled') this.error.broadcast(result);
            return acc;
          }

          // broadcast re-generation errors
          if (result.staleError) this.error.broadcast(result.staleError);
          return acc.concat(result.media);
        }, []);

        this.dynamicContent = this.dynamicContent
          .concat(media)
          .sort((a, b) => parseInt(b.created_time) - parseInt(a.created_time))
          .slice(0, num);

        this._refresh();
      })
      .catch(err => {
        if (err.message !== 'Canceled') console.error(err);
      });
  }

  metadata(itemId, key, value) {
    const model = this.ContentModel.find(itemId);
    return model._metadata(key, value);
  }

  // generates a content item from declarative metadata
  _mapDeclarative(item) {
    if (!item.metadata || item.metadata.service !== 'declarative') return item;

    // first copy all of the attributes except the mapped ones
    for (let attr in item.metadata) {
      if (attr === 'service') continue;
      if (this.mappedAttributes.includes(attr)) continue;
      item[attr.replace('data-', '')] = item.metadata[attr];
    }

    // then apply the mapping (non-mapped values take precedence)
    for (let attr in this.declarativeMap) {
      if (item[attr]) continue;
      item[attr] = item.metadata[this.declarativeMap[attr]];
    }

    return item;
  }
}

export default ContentStore;
