import { default as container, config } from './services/container';

import SkinStore from './services/skin-store';
import ContentStore from './services/content-store';
import DataStore from './services/data-store';

import FontService from './services/fonts';

import AutoscaleService from './services/autoscale';
import AdaptiveService from './services/adaptive';

import EventsStore from './services/events-store';

const diff = function(a, b) {
  const ids = b.map(i => i.id);
  return a.filter(i => !ids.includes(i.id));
};

/**
 * Base Widget
 *
 * @example
 * // import polyfills
 * import 'web-animations-js';
 * import cssVarPoly from 'Utils/css-variables-polyfill';
 * *
 * // configure the service container
 * import { config, ImageService } from 'Services';
 * config.service('Images', ImageService);
 *
 * import WidgetBase from '@widgetic/components/widget';
 * import MainComponent from './Index.html';
 * export default class Widget extends WidgetBase {
 *   // define the main component for this widget
 *   getComponent() {
 *     return MainComponent;
 *   }
 *
 *   // run the css variables polyfill
 *   added() {
 *     super.added();
 *     cssVarPoly.init();
 *   }
 *
 *   _onUpdate(key, value) {
 *     super._onUpdate(key, value);
 *     cssVarPoly.init();
 *   }
 * }
 */
class WidgetController {
  constructor({ metadata }, skin, content) {
    // console.log("Widget constructor:", metadata);

    this.metadata = metadata;
    this.content = content;

    config.constant('metadata', metadata);
    config.constant('skinMeta', metadata.widget.skinMeta);
    config.constant('initialSkin', skin);
    config.constant('contentMeta', metadata.widget.contentMeta);

    config.service('Skin', SkinStore, 'skinMeta', 'initialSkin');
    config.service('Content', ContentStore, 'contentMeta', 'contentSources', 'declarativeMap', 'ContentModel');
    config.service('Data', DataStore, 'metadata');

    config.service('Fonts', FontService, 'Skin');
    config.service('Autoscale', AutoscaleService, 'autoscale');
    config.service('Adaptive', AdaptiveService, 'adaptive');
    config.service('Events', EventsStore);

    config.constant(
      'editMode',
      metadata.mode === Widgetic.UI.Composition.EDIT_MODE
    );
    config.constant(
      'autoscale',
      metadata.embedOptions.autoscale &&
        metadata.embedOptions.autoscale !== 'off'
    );
    config.constant(
      'adaptive',
      parseInt(metadata.embedOptions.adaptive, 10) || 0
    );

    container.Skin.updates.listen(this._onUpdate.bind(this, 'skin'));
    container.Content.updates.listen(this._onUpdate.bind(this, 'content'));
  }

  getComponent() {
    throw new Error('Must extend and implement');
  }

  resize() {}
  clearContent() {
    // console.log("widget Base: clearContent!");
    if(this.widget && this.widget.clearContent) return this.widget.clearContent();
  }

  addContent(item) {
    // console.log("widget Base: item that was added:", item);
    if(this.widget && this.widget.addContent) return this.widget.addContent(item);
  }

  removeContent(item) {
    // console.log("widget base item that was deleted:", item);
    if(this.widget && this.widget.removeContent) return this.widget.removeContent(item);
  }

  on(eventName, callback) {
    return document.addEventListener(eventName, callback);
  }

  added() {
    // console.log("Widget Base added:", this.getPublicMethods());

    // create the widget main div el
    this.el = document.querySelector('.widget-container');
    this.el.setAttribute('widget', WIDGET_NAME);
    this.el.setAttribute('version', VERSION);

    // instantiate the main svelte component and add it inside the main el
    this.widget = new (this.getComponent())({
      target: this.el,
      props: {
        skin: container.Skin.get(),
        content: container.Content.get(),
      },
    });

    // convert the public methods from the child class
    let pubMeths = [];
    this.getPublicMethods().forEach(methodName => {
      if(this.widget[methodName]) pubMeths.push(this.widget[methodName].toString());
    });
    // console.log("Widget Base pubMeths:", pubMeths);

    // post message to parent window to publish the events and methods
    window.parent.postMessage(JSON.stringify({
      t: 'widgetPublicMethodsAndEvents', 
      id: window.name, 
      d: {
        compositionId: this.metadata.composition.id, 
        publicMethods: pubMeths, 
        publicEvents: this.getPublicEvents()
      }
    }), '*');
  }

  setSkin(skin) {
    container.Skin.set(skin);
  }

  _onUpdate(key, value) {

    // if the content changed
    if(key == 'content'){
      // call removeContent for the removedItem
      const removed = diff(this.content, value);

      if(removed.length == 1) {
        // console.log("Removed:", removed);
        this.removeContent(removed[0]);
      }

      // update the content here
      this.content = value;
    }

    // set the new value(svelte 3 set)
    this.widget.$set({ [key]: value });
  }
}

export default WidgetController;
